import { actions } from '../actions/Action';
import { newGallery, newCurrentReview, newCard as nc, newCard } from '../../BL/Restarts'
import { Mail } from '../../components/Email/Mail'
import { API_URL_BASE_CLIENT } from '../../config';
import $ from 'jquery'
import React from "react";
import cloneDeep from 'lodash/cloneDeep';
import { getFiles, setDataForFormData, resetFiles } from '../../Files/file'
import { getJwtFromCookie } from '../../cookie/cookie'
import Http, { HttpView } from '../../axios/axios'


const checkPermission = (result) => {
        if (result.status === 401) {
                result.data.routes ?
                        window.location.assign(`${API_URL_BASE_CLIENT}/login?des=${result.data.des}'&routes='${result.data.routes}`) :
                        window.location.assign(`${API_URL_BASE_CLIENT}/login?des=${result.data.des}`)
        }
}
export const getUid = ({ dispatch, getState }) => next => action => {
        if (action.type === 'UID_INIT') {
                var url = window.location;
                if (url.pathname.split('/')[1] === "card") {
                        let splidUrl = url.pathname.split('/');
                        const userName = splidUrl[2];
                        let cardName = splidUrl[3];
                        return HttpView.get(`/${userName}/${cardName}`)
                                .then((response) => {
                                        dispatch(actions.setCurrentCardPreview(response.data));
                                        dispatch(actions.setCardAfterServer(getState().card.currentCard));
                                })
                                .catch(function (error) {
                                        console.log(error.response);
                                })
                }
                else {
                        var userName = (url.pathname.split('/')[1]);
                        return Http.get(`${userName}/getUser/user/`)
                                .then((response) => {
                                        if (response.data === "undifined")
                                                window.location.href = `${API_URL_BASE_CLIENT}/login`;
                                        else {
                                                dispatch(actions.setUserId(response.data.user.uid));
                                                dispatch(actions.setIsPaying(response.data.user.isPaying));
                                                dispatch(actions.setUserName(userName));
                                                dispatch(actions.setFullname(userName));
                                                dispatch(actions.setPersonalemail(response.data.user.email));
                                                dispatch(actions.setEmail(response.data.user.email));
                                                dispatch({ type: 'CARD_INIT' });
                                        }
                                        return;
                                }).catch(function (error) {
                                        // handle error

                                        checkPermission(error.response);
                                        console.log(error);
                                })
                }
        }
        return next(action);
};

export const getCards = ({ dispatch, getState }) => next => action => {
        if (action.type === 'CARD_INIT') {
                let userName = getState().user.userName
                return Http.get(`/${userName}`).then((response) => {
                        dispatch(actions.setCards(response.data));
                }).catch((err) => {
                        console.log(err)
                })
        }
        return next(action);
};

export const cardSelection = ({ dispatch, getState }) => next => action => {
        if (action.type === 'SET_CARD') {
                dispatch(actions.setCurrentCard(action.payload));
                dispatch(actions.setCardAfterServer(getState().card.currentCard));
                resetFiles();
                return;
        }
        return next(action);
};

export const createNewCard = ({ dispatch, getState }) => next => action => {
        if (action.type === 'SET_NEW_CARD') {
                dispatch(actions.setSaveCard(action.payload));
                var agentDetails = getState().card.currentCard.agentDetails;
                dispatch(actions.setcurrentAgentForCard(agentDetails));
                dispatch(actions.setCurrentGallery(newGallery()));
                dispatch(actions.setCurrentReviews(newCurrentReview()));
                resetFiles();
                return;
        }
        return next(action);
};
// add
export const saveNewCard = ({ dispatch, getState }) => next => action => {

        if (action.type === 'SAVE_CARD' && getState().validation.formValid) {
                const uid = getState().user.userId
                const userName = getState().user.userName;
                dispatch(actions.setCardBeforeServer())
                dispatch(actions.setLoading());
                let formData = getFiles();
                $.ajax({
                        url: 'https://knowme.page/api/digitalcard/' + userName + '/' + uid + '/upload',
                        method: 'post',
                        contentType: false,
                        processData: false,
                        headers: {
                                // "authorization":"eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1aWQiOiJjRXJtYnQzRVlCYWg5SlFJaXlGUlhiSEdqMnUyIiwiZW1haWwiOiJhdGFyYUBsZWFkZXIuY29kZXMiLCJpcCI6IjJhMDE6NjUwMDphMDM4OmI3MDQ6YjdjYjo4NzQ1OjM2OGM6NTEwNiIsImlhdCI6MTYwOTA1OTE1Nn0.8Uuko7Iu7e22tB1jNZYaz6DF_iZyKAfL5NWb7fWtA74"
                                "authorization": getJwtFromCookie('jwt')
                        },
                        data: formData,
                        success: async function (data) {
                                const newCard = cloneDeep(getState().card.currentCard);
                                setDataForFormData(data.files, newCard)
                                resetFiles()
                                Http.post(`/${userName}`, JSON.stringify(newCard))
                                        .then((response) => {
                                                dispatch(actions.addNewCurrentCard(response.data));
                                                dispatch(actions.setCardAfterServer(response.data));
                                                dispatch(actions.setPublishStatus(1));
                                                dispatch(actions.setalertLink(true));
                                                dispatch(actions.setLoading());

                                        })
                                        .catch((err) => {
                                                console.log(err)
                                        })
                        }
                });
        }
        return next(action);
};





//update
export const updateCard = ({ dispatch, getState }) => next => action => {
        if (action.type === 'UPDATE_CARD' && getState().validation.formValid) {
                dispatch(actions.setCardBeforeServer())
                dispatch(actions.setLoading());
                const uid = getState().user.userId
                const userName = getState().user.userName;
                let formData = getFiles();
                $.ajax({
                        url: 'https://knowme.page/api/digitalcard/' + userName + '/' + uid + '/upload',
                        method: 'post',
                        contentType: false,
                        processData: false,
                        headers: {
                                // "authorization":"eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1aWQiOiJjRXJtYnQzRVlCYWg5SlFJaXlGUlhiSEdqMnUyIiwiZW1haWwiOiJhdGFyYUBsZWFkZXIuY29kZXMiLCJpcCI6IjJhMDE6NjUwMDphMDM4OmI3MDQ6YjdjYjo4NzQ1OjM2OGM6NTEwNiIsImlhdCI6MTYwOTA1OTE1Nn0.8Uuko7Iu7e22tB1jNZYaz6DF_iZyKAfL5NWb7fWtA74"
                                "authorization": getJwtFromCookie('jwt')
                        },
                        data: formData,
                        success: async function (data) {
                                const newCard = cloneDeep(getState().card.currentCard);
                                await setDataForFormData(data.files, newCard)
                                resetFiles();
                                Http.put(`/${userName}/${newCard._id}`, JSON.stringify(newCard))
                                        .then((response) => {
                                                dispatch(actions.updateCurrentCard(newCard));
                                                dispatch(actions.setCardAfterServer(newCard));
                                                dispatch(actions.setalertLink(true));
                                                dispatch(actions.setLoading());
                                        })
                                        .catch((err) => {
                                                console.log(err)
                                        })
                                return;
                        }
                })
        }
        return next(action);
};
// delete
export const deleteCard = ({ dispatch, getState }) => next => action => {

        if (action.type === 'DELETE_CARD') {
                const userName = getState().user.userName
                const newCard = getState().card.cards[action.payload];//-cards[index]
                Http.post(`/${userName}/${newCard._id}/delete`, JSON.stringify(newCard))
                        .then((response) => {
                                dispatch(actions.setNewCard(nc()));
                                dispatch(actions.deleteNewCard(response));
                                // dispatch(actions.setalertLink(false));
                        })
                        .catch((err) => {
                                console.log(err);
                        })
                return;
        }
        return next(action);
};

export const setCardBeforeServer = ({ dispatch, getState }) => next => action => {
        if (action.type === 'SET_CARD_BEFORE_SERVER') {
                var agentDetails = getState().agentDetails.currentAgentForCard;
                dispatch(actions.setAgentCard(agentDetails));
                var gallery = getState().gallerySlide.currentGallery;
                dispatch(actions.setGallleryCard(gallery));
                var review = getState().reveiws.currentReviews;
                dispatch(actions.setReviewCard(review))
        }
        return next(action);
};

export const setCardAfterServer = ({ dispatch }) => next => action => {
        if (action.type === 'SET_CARD_AFTER_SERVER') {
                dispatch(actions.setcurrentAgentForCard(action.payload.agentDetails));
                dispatch(actions.setCurrentGallery(action.payload.gallery));
                dispatch(actions.setCurrentReviews(action.payload.reveiw));
        }
        return next(action);
};

export const checkUniqueCardName = ({ dispatch, getState }) => next => action => {

        if (action.type === "CHECK_UNIQUE_CARD_NAME") {

                const userName = getState().user.userName;
                let data = {
                        cardname: action.payload.cardName,
                        userName: userName
                }
                Http.post(`/${userName}/checkUniqueCardName`, data)
                        .then(res => {
                                if (res.data === true) {
                                        dispatch(actions.setCardNameShow(false));
                                        if (action.payload.index !== -1) {
                                                //arrived from all cards
                                                let cardId = getState().card.cards[action.payload.index]._id;
                                                Http.post(`/${userName}/editCardName`, {
                                                        cardId: cardId,
                                                        cardName: action.payload.cardName
                                                })
                                        }
                                        else {
                                                //arrived from save button
                                                dispatch(actions.saveCard());
                                        }
                                }
                                else {
                                        dispatch(actions.setErrorMassage("The card name is exist, please enter another name"))
                                }
                        })
                        .catch(err => console.log(err));
        }
        return next(action);

}

// sendEmail in preview
export const sendEmail = ({ getState }) => next => action => {
        if (action.type === 'SEND_EMAIL') {
                const { renderToString } = require('react-dom/server');
                const massege = renderToString(<Mail header={"A customer makes contact"} body={[getState().card.currentCard.fullNameFooter, getState().card.currentCard.mobileFooter, getState().card.currentCard.personalEmailFooter, getState().card.currentCard.messageFooter]}></Mail>)
                const mailTo = getState().agentDetails.currentAgentForCard.personalEmail;
                HttpView.post('/', JSON.stringify({ "body": massege, "mailTo": mailTo }))
                        .then((response) => {
                                console.log(response);
                        })
                        .catch((err) => {
                                console.log(err)
                        })
        }
        return next(action);
};


