import React, { Component } from 'react';
import './custom.css'
import { Provider } from 'react-redux';
import { API_URL_BASE_CLIENT } from './config';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import store from './Redux/Stors/AgentStore';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { fas } from '@fortawesome/free-solid-svg-icons'
import {far } from '@fortawesome/free-regular-svg-icons'
import NewWrap from './components/Frame/NewWrap'
import Card from './components/card/Card'
import CardNameModal from './Modals/CardNameModal'
import { connect } from 'react-redux';
import CardPreview from './components/card/CardPreview';
import { actions } from "./Redux/actions/Action";

import uploadFile from './Modals/UploadImg/uploadFile';

library.add(fab, fas,far)

class App extends Component {
  render() {
    const {setImgModalShow}=this.props;
    return (
      <div className="container-fluid p-0 m-0  ">
        <Provider store={store}>
          <Router>
            <Switch>
              {/* view */}
              <Route path={`/card/:uid/:cardId`}>
                <div id="View" className="container-fluid " style={{ height: "100vh", overflowY: "auto", background: "rgb(235, 234, 234)" }}>
                  <Card showState={true}//view
                  ></Card>
                </div>
              </Route>
              {/* preview */}
              <Route path={`/preview/:screenSize`}>
                <div className="container-fluid " style={{ height: "100vh", overflow: "auto" }}>
                  <CardPreview {...this.props}></CardPreview>
                </div>
              </Route>

              <Route path="/:userName">
                <NewWrap />
                <CardNameModal/>
              </Route>
              <Route path='/' component={() => {
                window.location.href = `${API_URL_BASE_CLIENT}/login`;
                return null;
              }} />

            </Switch>

          </Router>

        </Provider>
      </div >
    );
  }
}
const mapStateToProps = (state) => {
  return {
    screenSize: state.card.screenSize,
    card: state.card,
    uploadFileFlag:state.modal.imgModal.show
  };
}

const mapDispatchToProps = (dispatch) => ({
  setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),
})
export default connect(mapStateToProps,mapDispatchToProps)(App);