import React from 'react';
import { OnOff } from './OnOff'
import { IconPicker } from 'react-fa-icon-picker'
import Tooltip from '@material-ui/core/Tooltip';
import '../../Css/Nav.css'
import './Contact.css'

export default function Contact(props) {
    const { title, icon, url, onChange, cheak, index, validationOnChange, typeInput, name, error, onChangeOnOff, onChangeIconSocialMedia, onChangeTitle } = props;
    return (
        <div className="container-fluid">
            <div className="row">
               
                    <Tooltip title="Change Icon" arrow>
                        <div className="col-2 p-0 icon_hover">
                            <IconPicker
                                containerStyles={{ background: "#ded5d5", maxHeight: "200px", border: "none" }}
                                buttonStyles={{
                                    background: "transparent", position: "relative",
                                    padding: "0px !important",
                                    width: "0px !important",
                                    minHeight: "20px !important",
                                    justifyContent: " left !important",
                                    alignItems: "center !important",
                                    border: "none !important"
                                }}
                                buttonIconStyles={{ marginTop: "-8px", color: "#FFF" }}
                                pickerIconStyles={{ color: "#3A405E" }}
                                searchInputStyles={{ height: "30px" }}
                                value={icon}
                                title={"change Icon"}
                                onChange={(v) => { onChangeIconSocialMedia(v, index) }}
                            />
                            {/* */}
                        </div></Tooltip> 
                <input
                    className="col-6 p-0  title"
                    onChange={(e) => onChangeTitle(e.target.value, index)}
                    value={title}
                    style={{
                        display: "block !important",
                        background: "transparent",
                        border: "none"
                    }}
                    title={"change link name"}
                    autoComplete="new-password"

                >
                </input>
                <div autoComplete="new-password"
                    className="col-4 p-0 d-flex justify-content-end">
                    <Tooltip title="Attach url" arrow>
                        <div>
                            <OnOff
                                idElement={title}
                                cheak={cheak}
                                onChangeOnOff={onChangeOnOff}
                                index={index}
                            // titleOnOff={"Attach url"}
                            ></OnOff></div></Tooltip>
                </div>
            </div>
            <div className="row  d-flex flex-column">
                <input
                    id={title}
                    className="input "
                    placeholder={icon === "FaWhatsapp" ? "Attach Namber With Prefix" : "Each Line Url"}
                    value={url ? url : ""}
                    onChange={(e) => {
                        onChange(e.target.value, index);
                        onChangeOnOff(true, index)
                        validationOnChange({ event: e, textName: title })
                    }}
                    type={typeInput ? typeInput : "text"}
                    name={name ? name : "name"}
                    required
                    noValidate
                ></input>
                {/* <span className='error'>{error}</span> */}
            </div >
        </div >
    );
};



