import React from 'react';
import SocialMediaCard from './SocialMediaCard'
import { connect } from 'react-redux';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { actions } from '../Redux/actions/Action';
import './Card.css'
import '../../Css/default_card_designs.css'
import { actions } from '../../Redux/actions/Action'
import SlideGallery from '../Gallery/SlideGallery'
import ImageGridList from '../Gallery/GridGallery'
import ReveiwsDesktop from '../Reveiws/ReveiwsDesktop'
import keys from '../../config/keys';


class BodyDesktop extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            aboutStatus: 0, //0-close 1-aboutMe 2-aboutUs
            fellowName: "",
            fellowEmail: "",
            fellowMobile: "",
            massegeBody: "",
            titleFooter: "",
        };
    }

    componentDidMount() {
        console.log("key", keys.API_URL)
    }

    setFellowDetails = (e) => {
        switch (e.target.id) {
            case "fellowName": {
                this.setState({ fellowName: e.target.value }); break;
            }
            case "fellowEmail": {
                this.setState({ fellowEmail: e.target.value }); break;
            }
            case "fellowMobile": {
                this.setState({ fellowMobile: e.target.value }); break;
            }
            case "massegeBody": {
                this.setState({ massegeBody: e.target.value }); break;
            }
            case "titleFooter": {
                this.setState({ titleFooter: e.target.value }); break;
            }
        }
    }


    render() {
        let num = 10, rows = [];
        const {
            showState,
            cssObj,
            agentDetails,
            card,
            changeFullName,
            changeRule,
            setAgentDetailsToggle,
            reveiw,
            setReveiwsToggle,
            user,
            setSocialMediaToggle,
            gallery,
            setvCardStyleToggle,
            setGallerytitle,
            setGalleryStyleToggle,
            setAboutConnect,
            setAbouttitle,
            setFooterStyleToggle,
            preview,
            setReveiwsTitle
        } = this.props;

        return (
            <>
                <div className="row"
                    style={
                        cssObj ? cssObj.body_card_desktop.body ?
                            cssObj.body_card_desktop.body : null : null
                    }
                >
                    <div
                        style={
                            cssObj ? cssObj.body_card_desktop.body_card_desktop_inline ?
                                {
                                    background: cssObj.body_card_desktop.body_card_desktop_inline.background,
                                    minHeight: cssObj.body_card_desktop.body_card_desktop_inline.minHeight,
                                    width: cssObj.body_card_desktop.body_card_desktop_inline.width,
                                    marginTop: cssObj.body_card_desktop.body_card_desktop_inline.marginTop,
                                    borderRadius: cssObj.body_card_desktop.body_card_desktop_inline.borderRadius,
                                    zIndex: 0,
                                } : null : null
                        }
                        onClick={setvCardStyleToggle}
                        title={showState ? "" : "Change body color"}
                    >
                        <div
                            className="col d-flex flex-column  p-0"
                            style={
                                cssObj ? cssObj.body_card_desktop.body_card_desktop_inline_conect ?
                                    cssObj.body_card_desktop.body_card_desktop_inline_conect : null : null
                            }
                        >
                            {
                                !showState ?
                                    <>
                                        <input
                                            autoComplete="new-password"
                                            className="row m-0 p-0 input_card_desktop"
                                            placeholder={agentDetails.fullName !== "" ? "" : user.userName}
                                            value={agentDetails.fullName !== "" ? agentDetails.fullName : user.userName}
                                            onChange={(e) => changeFullName(e.target.value)}
                                            onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                            hidden={!card.currentCard.vCardStyle.fullNameShow}
                                            style={cssObj ? cssObj.body_card_desktop.fullname ? cssObj.body_card_desktop.fullname : null : null}
                                            title={showState ? "" : "Change name"}
                                            id={showState ? "fullName_p" : "fullName"}
                                        />
                                        <input
                                            autoComplete="new-password"
                                            className="row m-0 p-0 input_card_desktop"
                                            placeholder={agentDetails.rule !== "" ? "" : "Enter your role"}
                                            value={agentDetails.rule !== "" ? agentDetails.rule : "Enter your role"}
                                            onChange={(e) => changeRule(e.target.value)}
                                            onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                            hidden={!card.currentCard.vCardStyle.ruleShow}
                                            style={cssObj ? cssObj.body_card_desktop.role ? cssObj.body_card_desktop.role : null : null}
                                            title={showState ? "" : "Change role"}
                                            id={showState ? "role_p" : "role"}

                                        />
                                    </> :
                                    <>
                                        {
                                            agentDetails.fullName !== "" ?
                                                <label
                                                    className="row m-0 p-0 input_card_desktop"
                                                    hidden={!card.currentCard.vCardStyle.fullNameShow}
                                                    style={
                                                        cssObj ? cssObj.body_card_desktop.fullname ?
                                                            cssObj.body_card_desktop.fullname : null : null
                                                    }
                                                >
                                                    {agentDetails.fullName}
                                                </label> : ""
                                        }
                                        {
                                            agentDetails.rule !== "" ?
                                                <label
                                                    className="row m-0 p-0 input_card_desktop"
                                                    hidden={!card.currentCard.vCardStyle.ruleShow}
                                                    style={
                                                        cssObj ? cssObj.body_card_desktop.role ?
                                                            cssObj.body_card_desktop.role : null : null
                                                    }
                                                >
                                                    {agentDetails.rule}
                                                </label> : ""
                                        }
                                    </>
                            }

                            {
                                card.currentCard.vCardStyle.aboutShow ?
                                    <div
                                        className="row d-flex justify-content-center"
                                        onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}>
                                        <div className="w-75 d-flex flex-column" >
                                            {showState ?
                                                <div
                                                    style={{
                                                        textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                        color: cssObj.body_card_desktop.styleTitleBodyDesktop.color
                                                    }}
                                                    autoComplete="new-password"
                                                    className="about_input_card_desktop">
                                                    {
                                                        agentDetails.aboutTitle !== "" ?
                                                            agentDetails.aboutTitle : "About"
                                                    }
                                                </div> : <input
                                                    style={{
                                                        textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                        color: cssObj.body_card_desktop.styleTitleBodyDesktop.color
                                                    }}
                                                    autoComplete="new-password"
                                                    className="about_input_card_desktop"
                                                    onChange={(e) => setAbouttitle(e.target.value)}
                                                    value={agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "About my business"}
                                                    placeholder="About my business"
                                                    title={showState ? "" : "Change about title"}
                                                    id={showState ? "about_p" : "about"}
                                                />
                                            }
                                            {
                                                !showState ?
                                                    <textarea
                                                        className="rounded about_card_desktop"
                                                        title="Write about yourself or your business"
                                                        onChange={(e) => setAboutConnect(e.target.value)}
                                                        value={
                                                            agentDetails.aboutConnect !== "" ?
                                                                agentDetails.aboutConnect :
                                                                "Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit ame t, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At"
                                                        }
                                                    ></textarea> :
                                                    <p className="rounded about_card_desktop">
                                                        {agentDetails.aboutConnect}
                                                    </p>
                                            }
                                        </div>
                                    </div> : ""
                            }
                            {
                                gallery && gallery.galleryShow ?
                                    <div
                                        className="row d-flex justify-content-center mt-5"
                                        onClick={(e) => { e.stopPropagation(); setGalleryStyleToggle() }}
                                    >
                                        <div className="w-75 d-flex flex-column about_input_card_desktop" >
                                            {showState ? <div
                                                style={{
                                                    textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                    color: cssObj.body_card_desktop.styleTitleBodyDesktop.color
                                                }}
                                                autoComplete="new-password"
                                                className=" d-flex flex-column">
                                                {gallery.title !== "" ? gallery.title : ""}
                                            </div> : <input
                                                style={{
                                                    textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                    color: cssObj.body_card_desktop.styleTitleBodyDesktop.color
                                                }}
                                                className=" revewis_input_card_desktop"
                                                onChange={(e) => { setGallerytitle(e.target.value) }}
                                                title={showState ? "" : "Change Reveiws title"}
                                                value={gallery.title !== "" ? gallery.title : ""}
                                                placeholder="Gallery"
                                                id={showState ? "about_p" : "about"}
                                            ></input>}
                                        </div>
                                    </div> : ""
                            }
                            {
                                gallery.galleryShow ? gallery.state === 'grid' ?
                                    <div style={{ width: "88%", margin: "auto" }}>
                                        <ImageGridList  {...this.props} />
                                    </div> :
                                    <div style={{ width: "80%", margin: "auto" }}>
                                        <SlideGallery {...this.props} />
                                    </div> : ""
                            }

                            {/* </div> */}
                            {reveiw.reveiwShow ?
                                <div className="row d-flex justify-content-center mt-5"
                                    onClick={(e) => { e.stopPropagation(); setReveiwsToggle() }}>
                                    <div className="w-75 d-flex flex-column about_input_card_desktop" >
                                        {showState ? <div
                                            style={{
                                                textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                color: cssObj.body_card_desktop.styleTitleBodyDesktop.color
                                            }}
                                            autoComplete="new-password"
                                            className=" d-flex flex-column">
                                            {reveiw.titleReveiws !== "" ? reveiw.titleReveiws : ""}</div> : <input
                                                style={{
                                                    textAlign: cssObj.bodyCardMobile.styleTitleBody.textAlign,
                                                    color: cssObj.body_card_desktop.styleTitleBodyDesktop.color
                                                }}
                                                className=" revewis_input_card_desktop "
                                                onChange={(e) => { setReveiwsTitle(e.target.value) }}
                                                title={showState ? "" : "Change Reveiws title"}
                                                value={reveiw.titleReveiws !== "" ? reveiw.titleReveiws : ""}
                                                placeholder="Reveiws"
                                                id={showState ? "about_p" : "about"}
                                                placeholder="Reveiws"
                                            />
                                        }
                                    </div>
                                </div> : ""
                            }
                            {
                                reveiw.reveiwShow ?
                                    <div className="d-flex flex-column" style={{ width: "75%", margin: "auto" }}>
                                        <ReveiwsDesktop {...this.props} />
                                    </div> : ""
                            }

                <div className="row" style={{ marginTop: "20px" }}>
                    <div className="col d-flex justify-content-center">
                        <div className="row w-75">
                            {
                                card.currentCard.socialMedias.map((socialMedia, i) => {
                                    if (socialMedia.show === true) {
                                        num -= 1;
                                        return (
                                            <SocialMediaCard
                                                key={i}
                                                showState={showState}
                                                css={"socialMediaWidth"}
                                                onClickFunc={!showState ? setSocialMediaToggle : null}
                                                styleIcon={
                                                    cssObj ? cssObj.body_card_desktop.socialMedia ?
                                                        cssObj.body_card_desktop.socialMedia : null : null
                                                }
                                                title={socialMedia.name}
                                                icon={socialMedia.icon}
                                                url={socialMedia.url}
                                            />
                                        )
                                    }
                                    return "";
                                })
                            }
                            {/* {
                                num > 0 && !showState ?
                                    <SocialMediaCard
                                        css={"socialMediaWidth"}
                                        showState={showState}
                                        onClickFunc={!showState ? setSocialMediaToggle : null}
                                        styleIcon={cssObj ? cssObj.body_card_desktop.socialMedia ? cssObj.body_card_desktop.socialMedia : null : null}
                                        title={"Plus"}
                                        icon={"FaPlus"}
                                        url={``}
                                    /> : ""
                            } */}
                            {rows}
                        </div>
                    </div>
                </div>

                <div
                    className=" footerDesktop d-flex justify-content-center align-items-center"
                    style={
                        cssObj ? cssObj.bodyCardMobile.background_footer ?
                            cssObj.bodyCardMobile.background_footer : null : null
                    }
                    onClick={(e) => { e.stopPropagation(); setFooterStyleToggle() }}
                >
                    <div className="col">
                        {
                            card.currentCard.vCardStyle.titleFooterShow ?
                                <div className=" row mt-4  d-flex justify-content-center" >
                                    {
                                        !showState || !preview ?
                                            <input
                                                autoComplete="new-password"
                                                className="d-flex justify-content-center rounded border-0 "
                                                placeholder=""
                                                value={cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ? cssObj.bodyCardMobile.footerMobile.titleFooter : ""}
                                                onChange={(e) => this.props.changeTitleFooterMobile(e.target.value)}
                                                style={{
                                                    background: cssObj.bodyCardMobile.input_footer_title.background,
                                                    color: cssObj.bodyCardMobile.input_footer_title.color,
                                                    textAlign: cssObj.bodyCardMobile.input_footer_title.textAlign,
                                                    font: cssObj.bodyCardMobile.input_footer_title.font,
                                                    letterSpacing: cssObj.bodyCardMobile.input_footer_title.letterSpacing,
                                                    textTransform: cssObj.bodyCardMobile.input_footer_title.textTransform,
                                                    fontSize: "30px"
                                                }}
                                                onClick={setFooterStyleToggle}
                                            /> :
                                            <label
                                                className="d-flex justify-content-center rounded border-0 "
                                                style={{
                                                    background: cssObj.bodyCardMobile.input_footer_title.background,
                                                    color: cssObj.bodyCardMobile.input_footer_title.color,
                                                    textAlign: cssObj.bodyCardMobile.input_footer_title.textAlign,
                                                    font: cssObj.bodyCardMobile.input_footer_title.font,
                                                    letterSpacing: cssObj.bodyCardMobile.input_footer_title.letterSpacing,
                                                    textTransform: cssObj.bodyCardMobile.input_footer_title.textTransform,
                                                    fontSize: "30px"
                                                }}
                                            >
                                                {
                                                    cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ?
                                                        cssObj.bodyCardMobile.footerMobile.titleFooter : ""
                                                }
                                            </label>
                                    }

                                </div> : ""
                        }

                        <div
                            className="row  mt-2 d-flex justify-content-center"
                            style={{ width: "90%", margin: "auto" }}
                        >
                            {
                                card.currentCard.vCardStyle.fullNameFooterShow ?
                                    <input
                                        id="fellowName"
                                        className="rounded border-0 col p-3 "
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.input_footer_background ?
                                                cssObj.bodyCardMobile.input_footer_background : null : null
                                        }
                                        placeholder="Full name"
                                        value={
                                            card.currentCard.fullNameFooter !== "" ?
                                                card.currentCard.fullNameFooter : ""
                                        }
                                        onChange={(e) => this.props.changeFullNameFooterMobile(e.target.value)}
                                        autoComplete="new-password"
                                    /> : ""
                            }
                            {
                                card.currentCard.vCardStyle.personalEmailFooterShow ?
                                    <input
                                        id="fellowEmail"
                                        className="rounded border-0 col ml-1"
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.input_footer_background ?
                                                cssObj.bodyCardMobile.input_footer_background : null : null
                                        }
                                        placeholder="e mail"
                                        value={
                                            card.currentCard.personalEmailFooter !== "" ?
                                                card.currentCard.personalEmailFooter : ""
                                        }
                                        onChange={(e) => this.props.changePersonalEmailFooterMobile(e.target.value)}
                                        type="email"
                                        autoComplete="new-password"
                                    ></input> : ""
                            }
                            {
                                card.currentCard.vCardStyle.mobileFooterShow ?
                                    <input
                                        id="fellowMobile"
                                        className="rounded border-0 col ml-1 p-3"
                                        style={
                                            cssObj ? cssObj.bodyCardMobile.input_footer_background ?
                                                cssObj.bodyCardMobile.input_footer_background : null : null
                                        }
                                        placeholder="Mobile"
                                        value={
                                            card.currentCard.mobileFooter !== "" ?
                                                card.currentCard.mobileFooter : ""
                                        }
                                        onChange={(e) => this.props.changeMobileFooterMobile(e.target.value)}
                                        type="tel"
                                        autoComplete="new-password"
                                    /> : ""
                            }
                        </div>
                        {
                            card.currentCard.vCardStyle.messageFooterShow ?
                                <div
                                    className="rounded border-0 row mt-2 d-flex justify-content-center"
                                    style={{ width: "90%", margin: "auto" }}
                                >
                                    <textarea
                                        id="massegeBody"
                                        className="rounded row p-3"
                                        style={{
                                            background: cssObj.bodyCardMobile.message_footer_background.background,
                                            width: cssObj.bodyCardMobile.message_footer_background.width,
                                            height: "100px",
                                        }}
                                        value={card.currentCard.messageFooter !== "" ? card.currentCard.messageFooter : ""}
                                        onChange={(e) => this.props.changeMessageFooterMobile(e.target.value)}
                                        // onClick={setFooterStyleToggle}
                                        placeholder="Message..."
                                    ></textarea>
                                </div> : ""
                        }
                        <div
                            className="row rounded d-flex justify-content-around mt-2"
                            style={{ width: "90%", margin: "auto" }}
                        >
                            <div
                                className="d-flex justify-content-center rounded mb-2"
                                onClick={showState || preview ? this.props.sendEmail : ""}
                                style={
                                    cssObj ? cssObj.bodyCardMobile.button_footer ?
                                        cssObj.bodyCardMobile.button_footer : null : null
                                }
                            >
                                {
                                    !showState && !preview ?
                                        <input
                                            autoComplete="new-password"
                                            className="rounded border-0"
                                            type="text"
                                            hidden={!card.currentCard.vCardStyle.buttonFooterShow}
                                            value={
                                                cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ?
                                                    cssObj.bodyCardMobile.footerMobile.buttonFooter : ""
                                            }
                                            onChange={(e) => this.props.changeButtonFooterMobile(e.target.value)}
                                            style={cssObj ? cssObj.bodyCardMobile.input_footer_button ? cssObj.bodyCardMobile.input_footer_button : null : null}
                                        /> :
                                        <label
                                            className="rounded border-0"
                                            style={
                                                cssObj ? cssObj.bodyCardMobile.input_footer_button ?
                                                    cssObj.bodyCardMobile.input_footer_button : null : null
                                            }
                                        >
                                            {
                                                cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ?
                                                    cssObj.bodyCardMobile.footerMobile.buttonFooter : ""
                                            }
                                        </label>
                                }
                            </div>
                        </div>
                        {
                            !user.isPaying ?
                                <div className="signature">
                                    <span> Made With </span>
                                    <FontAwesomeIcon
                                        className="heart-icon"
                                        icon={['fas', 'heart']}
                                        size="xs"
                                    />&nbsp;
                                    <span>
                                        by &nbsp;
                                        <a href="https://knowme.page/" className="signature-text">
                                            Knowme.page
                                        </a>
                                    </span>
                                </div> : ""
                        }
                    </div>
                </div>
            </div>
                    </div >
                </div >
            </>
        );
    }
}

const mapDispatchToProps = (dispatch) => ({

    changeTitleFooter: (name) => dispatch(actions.setTitleFooter(name)),
    changeFullNameFooter: (name) => dispatch(actions.setFullNameFooter(name)),
    changePersonalEmailFooter: (name) => dispatch(actions.setPersonalEmailFooter(name)),
    changeMobileFooter: (name) => dispatch(actions.setMobileFooter(name)),
    changeMessageFooter: (name) => dispatch(actions.setMessageFooter(name)),
    changeButtonFooter: (name) => dispatch(actions.setButtonFooter(name)),


})
export default connect(null, mapDispatchToProps)(BodyDesktop);
