import React, { Component } from "react";
import { connect } from 'react-redux';
import { Link } from "react-router-dom";
import { API_URL_BASE_CLIENT } from '../config';

import { actions } from '../Redux/actions/Action';
import { newCard } from '../BL/Restarts'
import AlertDelete from './alert/AlertDelete';
import FileCopyIcon from '@material-ui/icons/FileCopy'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import DeleteIcon from '@material-ui/icons/Delete';
import '../Css/Cards.css'
import AddIcon from '@material-ui/icons/Add';
import '../Css/AllCards.css';
import '../Css/Templet/Templets.css'



class AllCards extends Component {
 
 
    render() {
        
        console.log(this.props.cards)

        return (
            //push
            this.props.cards ?
                <div className="container-fluid" >
                    <div className="row">
                        <Link to={`/${this.props.userName}`}>
                            <div
                                className="AddNew d-flex justify-content-center align-items-center "
                                onClick={() => this.props.createNewCard(newCard())}
                            >
                                <AddIcon className="iconAdd" ></AddIcon>
                            </div>
                            <div className=" d-flex justify-content-center">Add NeW Card</div>
                        </Link>
                    
                        {
                            this.props.cards.map((card, i) => {
        
                                let index = i;
                                return(
                                        card.isDelete===false?
                                        <div key={i} className="card-container">
                                            <Link
                                                key={i}
                                                to={`/${this.props.userName}`}
                                                onClick={() => this.props.fillCurrentCard(i)}
                                            >
                                                <div
                                                    className="cards p-3 d-flex justify-content-center"
                                                    style={{ background: this.props.cards[i].vCardStyle.cover.backgroundImage, backgroundSize: "cover", opacity: "0.5" }}
                                                ></div>
                                            </Link>

                                            <input
                                                className="input-card-name"
                                                value={
                                                    card["cardName"]&&card.cardName  ?
                                                    card.cardName : "NoName"
                                                }
                                                onChange={(e) => this.props.setCardNameShow({
                                                    status:true,
                                                    index:i
                                                })}
                                            />

                                            <div className="row d-flex justify-content-center">
                                                <DeleteIcon
                                                    className="CardIcon"
                                                    onClick={(e) => {
                                                        e.stopPropagation();
                                                        e.preventDefault();
                                                        this.props.setalertStatuseDelete(true);
                                                        this.props.setindexDelet(index)}
                                                    }
                                                ></DeleteIcon>
                                                <FontAwesomeIcon
                                                    title="Go to view"
                                                    color="gray"
                                                    onClick={(e) => {
                                                        e.stopPropagation();
                                                    //    window.open(`${API_URL_BASE_CLIENT}/card/${this.props.uid}/${this.props.cards[i]._id}`, "_blank")
                                                        let cardName=this.props.cards[i].cardName.split(" ").join("_");
                                                        window.open(`${API_URL_BASE_CLIENT}/card/${this.props.userName}/${cardName}`, "_blank")
                                                    }}
                                                    icon={['fas', 'external-link-alt']}
                                                ></FontAwesomeIcon>
                                                <FileCopyIcon
                                                    className="CardIcon"
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        this.props.fillCurrentCard(i);
                                                        this.props.addNewCurrentCard()
                                                    }}
                                                ></FileCopyIcon>
                                            </div>
                                            {
                                                this.props.alertStatuseDelete === true ||
                                                this.props.alertStatuseSucsses === true ?
                                                <AlertDelete ></AlertDelete> : ""
                                            }
                                        </div>:""
                                  
                                )
                            })
                        }
                    </div>

                </div> : ""
        );
    }
}

const mapStateToProps = (state) => {
    return {
        cards: state.card.cards,
        userName: state.user.userName,
        indexDelet:state.card.indexDelet,
        alertStatuseDelete: state.alert.alertStatuseDelete,
        alertStatuseSucsses: state.alert.alertStatuseSucsses,
        uid:state.user.userId
    };
}

const mapDispatchToProps = (dispatch) => ({

    fillCurrentCard: (index) => dispatch(actions.setCard(index)),
    addNewCurrentCard: () => dispatch(actions.saveCard()),
    deleteCard: (insdex) => dispatch(actions.deleteCard(insdex)),
    setindexDelet: (status) => dispatch(actions.setindexDelet(status)),

    setalertStatuseDelete: (status) => dispatch(actions.setalertStatuseDelete(status)),

    setalertStatuseSucsses: (status) => dispatch(actions.setalertStatuseSucsses(status)),
    createNewCard: (newCard) => dispatch(actions.setNewCard(newCard)),
    setCardNameShow:(obj)=>dispatch(actions.setCardNameShow(obj))
})


export default connect(mapStateToProps, mapDispatchToProps)(AllCards);
